import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

export default ({ data })  => (
  <Layout>
    <p>About {data.site.siteMetadata.title}</p>
    <p>some test demo apps</p>
  </Layout>
)

// this is a page query, it queries the data inside gatsby-config.js > siteMetadata
// Page queries live outside of the component definition — by convention at the end of a page component file — and are only available on page components.
export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`

//StaticQuery is a new API introduced in Gatsby v2 that allows non-page components (like your layout.js component), 
//to retrieve data via GraphQL queries.

